@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@100;300;400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{box-sizing:border-box;font-family:'IBM Plex Sans KR'}.search-container{display:flex;flex-direction:column;align-items:center;justify-content:center;background:#f12711;background:linear-gradient(to right, #f5af19, #f12711);font-weight:700;font-size:3rem;color:#ffffff}@media screen and (max-width: 400px){.search-container{font-size:2.4rem}}@media screen and (min-width: 900px){.search-container{font-size:3.6rem}}.search-container input{width:400px;height:50px;margin-top:-45px;margin-bottom:45px;padding:10px;border:#e7e7e7 1px solid;border-radius:10px;font-size:1.1rem;outline:none}@media screen and (max-width: 400px){.search-container input{width:300px;height:40px;font-size:1rem}}@media screen and (min-width: 900px){.search-container input{width:600px;height:60px;font-size:1.2rem}}.recommendations-container{display:flex;flex-wrap:wrap;justify-content:center}.manga-card{display:flex;flex-direction:column;padding:10px;font-weight:600;color:#fffbfa}.manga-card p{padding:4px}

